import home from '../../components/view/home.vue'
import notice from '../../components/view/notice.vue'
import footerTabbar from '../../components/footer/footer-tabbar.vue'
import release from '../../components/view/release.vue'
export default {
	components: {
		home,
		notice,
		release,
		footerTabbar
	},
	data() {
		return {
			scrollY: 0,
			isTabShow: true,
			scrollBottom: false,
			prevTabID: 0,
			tabID: 1,
			loaded: [false,true,true, false, false]
		}
	},
	onLoad() {},
	onPageScroll(e) {
		this.scrollY = e.scrollTop
		this.scrollBottom = false
	},
	onReachBottom() {
		this.scrollBottom = true
	},
	methods: {
		switchTab(show){
			this.isTabShow = show
		},
		tabTap(index) {
			this.prevTabID = this.tabID
			this.tabID = index
			if (!this.loaded[index]) this.loaded[index] = true
		},
		closeReleaseTap(){
			this.tabID = this.prevTabID
		}
	}
}
